import * as React from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	Alert,
	IconButton,
	Grid,
} from '@material-ui/core';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { makeStyles } from '@material-ui/core/styles';
import { generatePath, useHistory } from 'react-router';
import { ArrangementOrder, HeadCell, TOffer } from '../../../types';
import Header from './ListHeader';
import LoadMore from '../components/LoadMore';
import { getOfferPrice } from '../../../helpers/offer';
import { formatCurrency } from '../../../helpers/currency';
import { formatDate } from '../../../helpers/date';
import { PATHS } from '../../../routes/path';

const useStyles = makeStyles(() => ({
	table: {
		minWidth: '750px',
	},
}));

type Props = {
	offers: Array<TOffer>;
	isLoading: boolean;
	hasMore?: boolean;
	loadMore?: Function;
	propertyId?: string;
	order?: ArrangementOrder;
	orderBy?: HeadCell<TOffer> | undefined;
	onRequestSort?: (p: HeadCell<TOffer>) => void;
};

const OffersTable = ({
	offers,
	isLoading,
	hasMore,
	loadMore,
	propertyId,
	order,
	orderBy,
	onRequestSort,
}: Props) => {
	const classes = useStyles();
	const history = useHistory();

	const editOffer = (offerId: string, offerPropertyId: string) => {
		const currentPropertyId = propertyId ?? offerPropertyId;
		const offerLink = generatePath(PATHS.PROPERTY_OFFER, {
			propertyId: currentPropertyId,
			offerId,
		});
		history.push(offerLink);
	};

	return (
		<>
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle' size='small'>
					<Header order={order} orderBy={orderBy} onRequestSort={onRequestSort} />
					<TableBody>
						{offers.map(offer => {
							if (typeof offer === 'number') return null;

							return (
								<TableRow hover tabIndex={-1} key={offer.OfferId}>
									<TableCell component='th'>{offer.OfferNo}</TableCell>
									<TableCell align='left'>
										{_get(offer, 'PropertyData.FullAddress') || 'N/A'}
									</TableCell>
									<TableCell align='left'>
										{_get(offer, 'PropertyData.OwnerNames') || 'N/A'}
									</TableCell>
									<TableCell align='left'>{getOfferPrice(offer) || 'N/A'}</TableCell>
									<TableCell align='left'>
										{offer.Funding?.PurchaseType || 'N/A'}
									</TableCell>
									<TableCell align='left'>{offer.Funding?.LoanType || 'N/A'}</TableCell>
									<TableCell align='left'>
										{formatCurrency(offer.Funding?.DownPayment) || 'N/A'}
									</TableCell>
									<TableCell sx={{ pr: 3 }} align='left'>
										{offer.Step?.messages?.advisor || 'N/A'}
									</TableCell>
									<TableCell align='left'>{formatDate(offer.CreatedAt)}</TableCell>
									<TableCell align='center' sx={{ pr: 3 }}>
										<IconButton
											color='primary'
											onClick={() => editOffer(offer.OfferId, offer.PropertyId)}
										>
											<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
										</IconButton>
									</TableCell>
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
			{_isEmpty(offers) && !isLoading && (
				<Grid item xs={12}>
					<Alert icon={false} severity='info'>
						No Records found
					</Alert>
				</Grid>
			)}
			{hasMore && loadMore && (
				<LoadMore onLoadMore={loadMore} loading={isLoading} />
			)}
		</>
	);
};

export default OffersTable;
