import _get from 'lodash.get';
import moment from 'moment';
import { formatCurrency, getOfferPriceValue } from '../../../helpers';
import { TOffer, HeadCell } from '../../../types';
import { TCounter } from './components/History/types';

export const CONTINGENCY_STATUS = {
	done: 'Done',
};

const CONTINGENCY_VALUES = {
	yes: 'Yes',
	no: 'No',
};

const DUE_DATE_FORMAT = 'MM/DD/YYYY';

const FUNDING_PURCHASE_TYPES = {
	cash: 'Cash',
	financing: 'Financing',
};

export const CONTINGENCY_TYPES = {
	saleOfHomeContingency: 'SaleOfHomeContingency',
	financeContingency: 'FinanceContingency',
	inspectionContingency: 'InspectionContingency',
	appraisalContingency: 'AppraisalContingency',
};

export const getContingencyStatus = (offer: TOffer, name: string) => {
	const contingency = _get(offer, `Contingencies.${name}`);
	if (!contingency || contingency === CONTINGENCY_VALUES.no)
		return {
			dueDate: 'N/A',
			status: 'N/A',
		};
	const uploadPA = _get(offer, `Contingencies.SignedPAConfirmedAt`);
	const duration = _get(offer, `Contingencies.${name}Days`);
	const isRemoved = _get(offer, ['Contingencies', `${name}Removed`]);
	return {
		status:
			isRemoved === CONTINGENCY_VALUES.yes
				? CONTINGENCY_STATUS.done
				: CONTINGENCY_VALUES.yes,
		dueDate: duration
			? moment(uploadPA).add(duration, 'days').format(DUE_DATE_FORMAT)
			: 'N/A',
	};
};

